.button-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.load-more-hidden {
  display: none;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1.2em;
  font-weight: bold;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: inline-block;
}

.load-more:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.load-more:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.photo-card {
  width: 300px;
  background-color: #fff;
  border-radius: 10px;
  margin: 20px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.photo-card img {
  width: 100%;
  height: 50%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.info {
  padding: 15px;
}

.info-item {
  margin: 5px 0;
  font-size: 18px;
}

.info-item span {
  color: #777;
  font-size: 16px;
}

